import React from "react";
import Charts from "./chart";
import { Container } from "mdbreact";
import { connect } from "react-redux";
import * as actions from "../../../../Actions/WeatherNow";

class WeatherCharts extends React.Component {
  state = {
    city: "city"
  };

  componentDidMount() {}
  render() {
    setTimeout(() => {
      console.log(this.props.state);
    }, 10000);
    return (
      <Container className="py-5" name="forecasts">
        <h2 className="display-4 cptl text-center mb-2">
          upcoming forecasts charts
        </h2>
        <h2 className="cptl text-center mb-5">
          {this.props.state.cityNameLoad ? this.props.state.cityName : ""}
        </h2>
        <div>
          <Charts />
        </div>
      </Container>
    );
  }
}


const mapStateToProps = state => {
  return {
    state: state
  };
};

const MapDispatchToprops = dispatch => {
  return {
    onCallCityName: dispatch(actions.callThisCITY())
  };
};
export default connect(
  mapStateToProps,
  MapDispatchToprops
)(WeatherCharts);
