import React from "react";
import { Input, InputGroup, Button } from "mdbreact";

class seatchform extends React.Component {
  render() {
    return (
      <div className="search-form w-75 ml-auto" className={this.props.className}>
        <form className="">
          <div className="position-relative">
            <Input type="text" className={"grenful-border " + this.props.className } label="Search" />
            <i
              className="material-icons position-absolute search-icon"
              style={{ top: "0.85vw", right: "0vw", zIndex: 5666 }}
            >
              search
            </i>
          </div>
        </form>
      </div>
    );
  }
}

export default seatchform;
