import React from "react";
import { Col, Container, Row, Button, MDBBtn, MDBIcon } from "mdbreact";
import HeaderImg from "./img1.jpg";

class Header extends React.Component {
  state = {};
  backgroundStyle = {
    backgroundImage: `url(${HeaderImg})`
  };

  render() {
    return (
      <header className="p-0" style={this.backgroundStyle}>
        <div className="overlay-hfa2">
          <Container
            className="form-container d-flex flex-column justify-content-center"
            style={{ minHeight: "inherit" }}
          >
            <Row className="ROW-MAB">
              <div className="col-12">
                <div>
                  <h2 className="display-4 white-color text-sm-left text-center">
                    Weather now is easy to understand with us
                  </h2>
                </div>
                <div className="find-location-form mt-5 position-relative ">
                  <form className="position-relative">
                    <div className="form-group">
                      <input
                        type="text"
                        className="form-control max-width80 mr-sm-auto mx-sm-0 mx-auto p-4 "
                        placeholder="Find Location Weather"
                      />
                      <Button className="find-icon position-absolute" floating>
                        <i className="material-icons mt-1">location_on</i>
                      </Button>
                    </div>
                  </form>
                </div>
              </div>
            </Row>
          </Container>
        </div>
      </header>
    );
  }
}

export default Header;
