import React from "react";
import { Link } from "react-scroll";
export const LiMembeer = props => {
  return (
    <Link to={props.scrollName} smooth={true} spy={true} offset={props.offset}>
      <li className="list-group-item d-flex flex-row justify-content-between ">
        <span className={"white-color mr-2 " + props.spanClass}>
          {props.conentlist}
        </span>
        <i className={"material-icons white-color " + props.classNameIcon}>
          {props.iconName}
        </i>
      </li>
    </Link>
  );
};

export const Menubar = props => {
  return (
    <div
      style={props.style}
      className={"dropdown-custom fixed-top rounded z-depth-2 "}
    >
      <ul className="list-group h-100 justify-content-center ">
        {props.children}
      </ul>
    </div>
  );
};
