
import MostlySunny from './assets/W_icons/MostlySunny.svg';
import PartyCloudy from './assets/W_icons/PartyCloudy.svg';
import MostlyCloudy from './assets/W_icons/MostlyCloudy.svg';
import brokenClouds from './assets/W_icons/brokenClouds.svg';
import ScatteredShowers from './assets/W_icons/ScatteredShowers.svg';
import Rain from './assets/W_icons/Rain.svg';
import thunderstorm from './assets/W_icons/ScatteredThunderstorm.svg';
import snow from './assets/W_icons/Snow.svg';
import mist from './assets/W_icons/Fog.svg';

/*          NIGHT ICONS         */

import MostlySunnyN from './assets/W_icons/ClearNight.svg';
import PartyCloudyN from './assets/W_icons/PartyCloudyNight.svg';
import MostlyCloudyN from './assets/W_icons/MostlyCloudyNight.svg';
import brokenCloudsN from './assets/W_icons/brokenCloudsnight.svg';
import ScatteredShowersN from './assets/W_icons/ScatteredShowersNight.svg';
import RainN from './assets/W_icons/RainNght.svg';
import thunderstormN from './assets/W_icons/ScatteredhunderstormNight.svg';
import snowN from './assets/W_icons/SnowNight.svg';
import mistN from './assets/W_icons/FogNight.svg';


//import err404 from './assets/W_icons/err404.svg';

export const SVGreactDay = status => {
  switch (status) {
    case "clear sky":
      return MostlySunny;
      break;
    case "few clouds":
      return PartyCloudy;
      break;
    case "scattered clouds":
      return MostlyCloudy;
      break;
    case "broken clouds":
      return brokenClouds;
      break;
    case "shower rain":
      return ScatteredShowers;
      break;
    case "rain":
      return Rain;
      break;
    case "thunderstorm":
      return thunderstorm;
      break;
    case "snow":
      return snow;
      break;
    case "mist":
      return mist;
      break;
    default:
      return "404.svg";
  }
};
export const SVGreactNight = status => {
  switch (status) {
    case "clear sky":
      return MostlySunnyN;
      break;
    case "few clouds":
      return PartyCloudyN;
      break;
    case "scattered clouds":
      return MostlyCloudyN;
      break;
    case "broken clouds":
      return brokenCloudsN;
      break;
    case "shower rain":
      return ScatteredShowersN;
      break;
    case "rain":
      return RainN;
      break;
    case "thunderstorm":
      return thunderstormN;
      break;
    case "snow":
      return snowN;
      break;
    case "mist":
      return mistN;
      break;
    default:
      return "404.svg";
  }
};
