import React from "react";
import { Container, Row, Col } from "mdbreact";
import SmallCard from "./weather.section";
import MainCard from "./mainCard";
import { connect } from "react-redux";
import { ClipLoader } from "react-spinners";

import * as action from "../../../../Actions/WeatherNow";

class WeatherCards extends React.Component {
  state = {
    isload: false,
    temp1: "",
    weatherDataWeeakly: ""
  };

  UnixToTextDay(unixdate) {
    const dayDate = new Date(unixdate * 1000);
    return dayDate.toString().substr(0, 3);
  }

  UnixToTexthour(unixdate) {
    const dayDate = new Date(unixdate * 1000);
    return (
      dayDate.toLocaleTimeString().substr(0, 4) +
      dayDate.toLocaleTimeString().substr(7, 8)
    );
  }

  renderLiList() {
    if (this.props.state.isload) {
      return this.props.state.weatherData.map((item, index) => {
        return (
          <SmallCard
            day={this.UnixToTextDay(item.dt)}
            key={index.toString()}
            hour={this.UnixToTexthour(item.dt)}
            temperature={Math.floor(item.main.temp - 273) + "\u00B0\u0043"}
            status="cloud"
          />
        );
      });
    } else {
      return <ClipLoader />;
    }
  }

  componentDidMount() {}

  render() {
    return (
      <Container className="py-5 weather-cards-container rounded my-5">
        <h1 className="display-4 text-center my-5 MerryFont">Weekly Weather</h1>
        <Row>
          <Col className="col-md-5 col-sm-12 col-12">
            <MainCard statusIcon="cloud" className="border-right pr-4" />
          </Col>
          <Col className="col-md-7 col-sm-12 col-12 d-flex flex-row justify-content-center">
            {this.renderLiList()}
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapPropsToState = state => {
  return {
    state: state
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetCityWeather: dispatch(action.DailyWeatherData())
  };
};

export default connect(
  mapPropsToState,
  mapDispatchToProps
)(WeatherCards);

