import React from "react";
class CityWeather extends React.Component {
  state = {};

  render(props) {
    return (
      <div className="p-3">
        <div
          className="mt-lg-3 mt-sm-2 mb-2 float-left mr-sm-4 mr-0 pt-1"
          style={{ overflow: "hidden" }}
        >
          <img className="imgOFweather" src={this.props.imgsource} title={this.props.title}/>
        </div>

        <div className="float-left">
          <h5 className="white-color">{this.props.country}</h5>
          <h3 className="h1-responsive cptl font-weight-bold white-color">
            {this.props.city}
          </h3>
          <h5 className="cptl h2-responsive white-color">{this.props.temp}</h5>
        </div>
        <div className="clearfix" />
      </div>
    );
  }
}

export default CityWeather;
