import React from "react";
import { Container, Row, Col } from "mdbreact";
import BoxContent from "./BoxContent";
import CityWeather from "./YrCityWeather";
import weathercall from "../../Apis/weatherApi";
import LoaderSpiner from "react-loader-spinner";
import AskingForCokies from "./AskForCookies";
import Cookiejs from "js-cookie";
import TimeNow from "./assets/timenow";
import { SVGreactDay, SVGreactNight } from "./weatherStatusImg";
import { Redirect } from "react-router-dom";

class Landpage extends React.Component {
  state = {
    position: [],
    weatherLandPage: [],
    loading: false,
    userAccept: "",
    loadingTime: true,
    acceptedCokkies: false,
    accptedCokkiesTwo: "true",
    dnone: "",
    weatherStatus: "",
    iconurl: "",
    endWaiting : false
  };

  TimeNOWW = () => {
    if (TimeNow() == "Day") {
      return SVGreactDay(this.state.weatherStatus);
    }
    if (TimeNow() == "Night") {
      return SVGreactNight(this.state.weatherStatus);
    }
  };
  settingCockiesTwo() {
    var trueq = this.state.accptedCokkiesTwo; //toString(this.state.acceptedCokkies);
    var date = new Date();
    date.setTime(date.getTime() + 5 * 24 * 60 * 60 * 1000);
    var expires = "expires = " + date.toUTCString();
    document.cookie = `userAcceptedCokiesTwo = ${trueq}; ${expires} + "path=*"`;
    setTimeout(()=>{
      Cookiejs.set("Location",this.state.weatherLandPage[0],{expires : 5 , path : "/"});
    },1000)

  }

  settingCockies = () => {
    var date = new Date();
    const position = [
      this.state.position.longitude,
      this.state.position.latitude
    ];
    date.setTime(date.getTime() + 5 * 24 * 60 * 60 * 1000);
    var expires = "expires = " + date.toUTCString();
    document.cookie = `position = ${position}; ${expires} + "path=/"`;
   
    this.setState({ acceptedCokkies: true });
    this.setState({ dnone: "d-none" });
    setTimeout(() => {
      return this.settingCockiesTwo();
    }, 1000);
  };

  getUserCordinates() {
    if (window.navigator.geolocation) {
      window.navigator.geolocation.getCurrentPosition(
        position => {
          this.setState({ userAccept: true });
          console.log(this.state.userAccept);
          this.setState({
            position: {
              longitude: position.coords.longitude,
              latitude: position.coords.latitude
            }
          });
          if (this.state.userAccept) {
            this.userAcceptingLocation();
            this.setState({ loadingTime: false });
          }
        },
        err => {
          this.setState({ userAccept: false });
          this.setState({endWaiting : true })
        },
        {
          enableHighAccuracy: true
        }
      );
    }
  }
  userAcceptingLocation() {
    let longitude = this.state.position.longitude,
      latitude = this.state.position.latitude;
    const apikey = "ae9ccb1f3500a6435f46b51eb13ae8b0";
    weathercall
      .get(`/weather?lat=${latitude}&lon=${longitude}&appid=${apikey}`)
      .then(res => {
        this.setState({
          weatherLandPage: [
            res.data.name,
            Math.round(res.data.main.temp - 273.15),
            res.data.sys.country,
            "\u0043\u00B0"
          ]
        });
        this.setState({ weatherStatus: res.data.weather[0].description });
        let iconsUrl = this.TimeNOWW();
        this.setState({ iconurl: iconsUrl });
      });
  }

  settingCockiesChanges = () => {
    let cok = Cookiejs.get("userAcceptedCokiesTwo");
    if (cok == "true") {
      return "d-none";
    } else {
      return "";
    }
  };
  handelingResponsive = () => {
    if (window.innerWidth < 567) {
      return <br />;
    } else {
      return " ";
    }
  };

  componentDidMount() {
    this.getUserCordinates();
    setTimeout(()=>{
      console.log(this.state)
    },2000)
  }

  rederictto404() {
    return <Redirect to="/404" />;
  }
  render() {
    if (this.state.loadingTime) {
      if (this.state.endWaiting) {
        return this.rederictto404();
      } else {
        return (
          <div className="spinner-container">
            <div
              className="position-absolute"
              style={{ top: "45%", left: "40%" }}
            >
              <LoaderSpiner
                type="Bars"
                color="#00BFFF"
                height="100"
                width="100"
              />
            </div>
          </div>
        );
      }
    } else {
      return (
        <Container fluid className="landpage">
          <Row className="d-flex flex-sm-row flex-column align-items-sm-center justify-content-center landpage-row">
            <Col sm={6} className="text-sm-left text-center">
              <BoxContent />
            </Col>
            <Col sm={6}>
              <CityWeather
                imgsource={this.state.iconurl}
                temp={
                  this.state.weatherLandPage[1] + this.state.weatherLandPage[3]
                }
                country={this.state.weatherLandPage[2]}
                city={this.state.weatherLandPage[0]}
                title={this.state.weatherStatus}
              />
            </Col>
          </Row>
          <AskingForCokies
            className={
              "position-absolute p-0 mx-auto alerto-container" +
              this.state.dnone +
              " " +
              this.settingCockiesChanges()
            }
            style={{ bottom: 0, left: "0vw", width: "100%" }}
            action={this.settingCockies}
            br={this.handelingResponsive()}
            id="propg"
          />
        </Container>
      );
    }
  }
}

export default Landpage;
