import React from "react";
import { Container, Row } from "mdbreact";
import images from "./images";
import CardStyle from "./card";
import { connect } from "react-redux";
import { PulseLoader } from "react-spinners";
import {
  loadCitesWeather_alex,
  loadCitesWeather,
  loadCitesWeather_SS,
  loadCitesWeather_ASWAN,
  loadCitesWeather_suez,
  loadCitesWeather_LUXOR,
  loadCitesWeather_MATROUH,
  loadCitesWeather_SHAR,
  loadCitesWeather_redsea
} from "./../../../../Actions/WeatherNow";
class CityCard extends React.Component {
  state = {
    hoverOn: false
  };

  HoverimgEffect() {
    if (this.state.hoverOn) {
      return "card-hover3D";
    }
    if (!this.state.hoverOn) {
      return " ";
    }
  }

  render() {
    if (this.props.state.isload_2) {
      return (
        <Container className="text-center cards pb-5 pt-2" name="timeline">
          <h2 className="display-4 MerryFont mb-5 ">Timeline</h2>
          <Row>
            <CardStyle
              city="Cairo"
              cityimage={images.cairo}
              cityTemp={
                Math.floor(this.props.state.cairo - 273) + "\u00B0\u0043"
              }
            />
            <CardStyle
              city="Alexendria"
              cityimage={images.alex}
              cityTemp={
                this.props.state.alexLoad
                  ? Math.floor(this.props.state.alex - 273) + "\u00B0\u0043"
                  : 0
              }
            />
            <CardStyle
              city="South Sini"
              cityimage={images.southSinai}
              cityTemp={
                this.props.state.ssload
                  ? Math.floor(this.props.state.southSinai - 273) +
                    "\u00B0\u0043"
                  : 0
              }
            />

            <CardStyle
              city="Aswan"
              cityimage={images.aswan}
              cityTemp={
                this.props.state.aswanLoad
                  ? Math.floor(this.props.state.aswan - 273) + "\u00B0\u0043"
                  : 0
              }
            />

            <CardStyle
              city="Luxor"
              cityimage={images.luxor}
              cityTemp={
                this.props.state.luxorload
                  ? Math.floor(this.props.state.luxor - 273) + "\u00B0\u0043"
                  : 0
              }
            />
            <CardStyle
              city="Suez"
              cityimage={images.suez}
              cityTemp={
                this.props.state.suezLoad
                  ? Math.floor(this.props.state.suez - 273) + "\u00B0\u0043"
                  : 0
              }
            />
            <CardStyle
              city="Red Sea"
              cityimage={images.redsea}
              cityTemp={
                this.props.state.redseaload
                  ? Math.floor(this.props.state.redsea - 273) + "\u00B0\u0043"
                  : 0
              }
            />
            <CardStyle
              city="Matroh"
              cityimage={images.matroh}
              cityTemp={
                this.props.state.matrouhload
                  ? Math.floor(this.props.state.matrouh - 273) + "\u00B0\u0043"
                  : 0
              }
            />
            <CardStyle
              city="AL-Sharqia"
              cityimage={images.sharqia}
              cityTemp={
                this.props.state.sharqiaload
                  ? Math.floor(this.props.state.sharqia - 273) + "\u00B0\u0043"
                  : 0
              }
            />
          </Row>
        </Container>
      );
    } else {
      return <PulseLoader />;
    }
  }
}

const mapPropsToState = state => {
  return {
    state: state
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onCairoWeather: dispatch(loadCitesWeather()),
    onAlexWeather: dispatch(loadCitesWeather_alex()),
    onSSweather: dispatch(loadCitesWeather_SS()),
    onAswanWeather: dispatch(loadCitesWeather_ASWAN()),
    onLuxorWeather: dispatch(loadCitesWeather_LUXOR()),
    onSuexWeather: dispatch(loadCitesWeather_suez()),
    onRedseaWeather: dispatch(loadCitesWeather_redsea()),
    onMatrouhWeather: dispatch(loadCitesWeather_MATROUH()),
    onSharWeather: dispatch(loadCitesWeather_SHAR())
  };
};

export default connect(
  mapPropsToState,
  mapDispatchToProps
)(CityCard);
