import React from "react";
import Landpage from "./Components/LandPage/LandPage";
import { BrowserRouter, Route, Router } from "react-router-dom";
import ER404 from "./Components/404ERR/ERR404";
import StaticSite from "./Components/Staticsite/static.site";

function App() {
  return (
    <Router>
      <BrowserRouter>
        <Route path="/" exact component={Landpage} />
        <Route path="/404/" component={ER404} />
        <Route path="/home/" component={StaticSite} />
      </BrowserRouter>
    </Router>
  );
}

export default App;

