const intialstate = {
  coords: "",
  coords_load: false,
  isload: false,
  weatherData: "",
  cityData: "",
  cityLoad: false,
  isload_2: false,
  cairo: "",
  alexLoad: false,
  alex: "",
  southSinai: "",
  ssload: false,
  aswan: "",
  aswanLoad: false,
  luxor: "",
  luxorload: false,
  suez: "",
  suezLoad: false,
  redsea: "",
  redseaload: false,
  matrouh: "",
  matrouhload: false,
  sharqia: "",
  sharqiaload: false,
  charts_load: false,
  temp: "",
  humidity: "",
  wind: "",
  hours: "",
  day: "",
  cityName: "",
  cityNameLoad: false
};

const MainReducer = (state = intialstate, action) => {
  if (action.type === "LOAD_COORDS") {
    return {
      ...state,
      coords_load: false
    };
  }

  if (action.type === "LOAD_COORDS") {
    return {
      ...state,
      coords: action.payload
    };
  }
  if (action.type === "FINISH_LOAD_COORDS") {
    return {
      ...state,
      coords_load: true
    };
  }

  if (action.type === "GET_WEATHER_CITY_DATA") {
    setTimeout(() => {
      state.isload = true;
    }, 2000);
    return {
      ...state,
      weatherData: [
        action.payload.list[5],
        action.payload.list[13],
        action.payload.list[21],
        action.payload.list[29],
        action.payload.list[37]
      ]
    };
  }
  if (action.type === "LOAD_WEATHER_DATA") {
    return {
      ...state,
      isload: false
    };
  }

  if (action.type === "DATA-CITY-NOW-START") {
    return {
      ...state,
      cityLoad: false
    };
  }
  if (action.type === "DATA-CITY-NOW") {
    return {
      ...state,
      cityData: action.payload
    };
  }
  if (action.type === "DATA-CITY-NOW-END") {
    return {
      ...state,
      cityLoad: true
    };
  }

  if (action.type === "FINISH_LOADING_DATA") {
    return {
      ...state,
      isload: true
    };
  }

  if (action.type === "GET_CAIRO_WEATHER") {
    return {
      ...state,
      cairo: action.payload
    };
  }

  if (action.type === "ALEX_LOAD") {
    return {
      ...state,
      alexLoad: false
    };
  }
  if (action.type === "ALEX_FINISH_LOAD") {
    return {
      ...state,
      alexLoad: true
    };
  }
  if (action.type === "GET_ALEX_WEATHER") {
    return {
      ...state,
      alex: action.payload
    };
  }
  if (action.type === "SS_START_LOAD") {
    return {
      ...state,
      ssload: false
    };
  }
  if (action.type === "SS_FINISH_LOAD") {
    return {
      ...state,
      ssload: true
    };
  }
  if (action.type === "SS_GET_DATA") {
    return {
      ...state,
      southSinai: action.payload
    };
  }
  if (action.type === "LOAD_WEAHTER_API_CITES") {
    return {
      ...state,
      isload_2: false
    };
  }
  if (action.type === "FINISH_LOADING_WEAHTER_API_CITES") {
    return {
      ...state,
      isload_2: true
    };
  }

  // aswan reducer

  if (action.type === "ASWAN_GET_DATA") {
    return {
      ...state,
      aswan: action.payload
    };
  }

  if (action.type === "ASWAN_START_LOAD") {
    return {
      ...state,
      aswanLoad: false
    };
  }
  if (action.type === "ASWAN_FINISH_LOAD") {
    return {
      ...state,
      aswanLoad: true
    };
  }

  // luxor

  if (action.type === "LUXOR_GET_DATA") {
    return {
      ...state,
      luxor: action.payload
    };
  }

  if (action.type === "LUXOR_START_LOAD") {
    return {
      ...state,
      luxorload: false
    };
  }
  if (action.type === "LUXOR_FINISH_LOAD") {
    return {
      ...state,
      luxorload: true
    };
  }

  // SUEZ

  if (action.type === "SUEZ_GET_DATA") {
    return {
      ...state,
      suez: action.payload
    };
  }

  if (action.type === "SUEZ_START_LOAD") {
    return {
      ...state,
      suezLoad: false
    };
  }
  if (action.type === "SUEZ_FINISH_LOAD") {
    return {
      ...state,
      suezLoad: true
    };
  }

  // Red sea reducer

  if (action.type === "REDSEA_GET_DATA") {
    return {
      ...state,
      redsea: action.payload
    };
  }

  if (action.type === "REDSEA_START_LOAD") {
    return {
      ...state,
      redseaload: false
    };
  }
  if (action.type === "REDSEA_FINISH_LOAD") {
    return {
      ...state,
      redseaload: true
    };
  }

  // MATROH

  if (action.type === "MATROUH_GET_DATA") {
    return {
      ...state,
      matrouh: action.payload
    };
  }

  if (action.type === "MATROUH_START_LOAD") {
    return {
      ...state,
      matrouhload: false
    };
  }
  if (action.type === "MATROUH_FINISH_LOAD") {
    return {
      ...state,
      matrouhload: true
    };
  }

  // SHAR

  if (action.type === "SHAR_GET_DATA") {
    return {
      ...state,
      sharqia: action.payload
    };
  }

  if (action.type === "SHAR_START_LOAD") {
    return {
      ...state,
      sharqiaload: false
    };
  }
  if (action.type === "SHAR_FINISH_LOAD") {
    return {
      ...state,
      sharqiaload: true
    };
  }

  // CHARTS REDUCER
  if (action.type === "START_CHARTS_LOAD") {
    return {
      ...state,
      charts_load: false
    };
  }
  /* for($i=0 ; $i<=39; $i+=4){
    echo $i;
} */

  if (action.type === "GET_CHARTS_DATA") {
    const handtype = action.payload.list;
    const fortemp = (hand, ident) => {
      let arra = [];
      for (let i = 5; i <= 39; i += 8) {
        arra.push(hand[i].main[ident]);
      }
      return arra;
    };

    const forWind = hand => {
      let arra = [];
      for (let i = 5; i <= 39; i += 8) {
        arra.push(hand[i].wind.speed);
      }
      return arra;
    };

    const forTimestamp = hand => {
      let arra = [];
      for (let i = 5; i <= 39; i += 8) {
        arra.push(hand[i].dt);
      }
      return arra;
    };
    const convertToCelsuis = arr => {
      const req = arr.map(item => {
        return Math.floor(item - 273);
      });
      return req;
    };

    const convertToDay = arr => {
      const req = arr.map(item => {
        const date = new Date(item * 1000);
        const dateToDay = date.toString().substr(0, 3);
        return dateToDay;
      });
      return req;
    };

    const convertTohour = arr => {
      const req = arr.map(item => {
        const date = new Date(item * 1000);
        const dateToTime = () => {
          const dateToHour = new Date(item * 1000).toLocaleTimeString()[1];
          if (typeof dateToHour === "number") {
            return [
              date.toLocaleTimeString().substr(0, 1),
              date.toLocaleTimeString().substr(1, 2),
              date.toLocaleTimeString().substr(8, 9)
            ];
          } else {
            return [
              date.toLocaleTimeString().substr(0, 1),
              date.toLocaleTimeString().substr(8, 9)
            ];
          }
        };
        return dateToTime();
      });
      return req;
    };
    return {
      ...state,
      humidity: fortemp(handtype, "humidity"),
      temp: convertToCelsuis(fortemp(handtype, "temp")),
      wind: forWind(handtype),
      day: convertToDay(forTimestamp(handtype)),
      hours: convertTohour(forTimestamp(handtype))
    };
  }

  if (action.type === "FINISH_CHARTS_LOAD") {
    return {
      ...state,
      charts_load: true
    };
  }

  if (action.type === "START_LOAD_CITY_NAME") {
    return {
      ...state,
      cityNameLoad: false
    };
  }
  if (action.type === "FINISH_LOAD_CITY_NAME") {
    return {
      ...state,
      cityNameLoad: true
    };
  }
  if (action.type === "LOADED_CITY_NAME") {
    return {
      ...state,
      cityName: action.payload
    };
  }
  return state;
};

export default MainReducer;
