import Axios from "axios";
export const DailyWeatherData = (lat, lon) => dispatch => {
  dispatch(LOAD_WEATHER_DATA());
  const apikey = "ae9ccb1f3500a6435f46b51eb13ae8b0";

  window.navigator.geolocation.getCurrentPosition(position => {
    Axios.get(
      `https://api.openweathermap.org/data/2.5/forecast?lat=${
        position.coords.latitude
      }&lon=${position.coords.longitude}&appid=${apikey}`
    ).then(data => {
      dispatch({
        type: "GET_WEATHER_CITY_DATA",
        payload: data.data
      });
      dispatch(FINISH_LOADING_DATA());
    });
  });
};

const nowCityTemp_start = () => {
  return {
    type: "DATA-CITY-NOW-START"
  };
};
const nowCityTemp_end = () => {
  return {
    type: "DATA-CITY-NOW-END"
  };
};
export const nowCityTemp = (lat, lon) => dispatch => {
  dispatch(nowCityTemp_start());
  const apikey = "ae9ccb1f3500a6435f46b51eb13ae8b0";
  window.navigator.geolocation.getCurrentPosition(position => {
    Axios.get(
      `https://api.openweathermap.org/data/2.5/weather?lat=${
        position.coords.latitude
      }&lon=${position.coords.longitude}&appid=${apikey}`
    ).then(res => {
      dispatch({
        type: "DATA-CITY-NOW",
        payload: res.data
      });
      dispatch(nowCityTemp_end());
    });
  });
};

