// cairo

export const LOAD_WEAHTER_API_CITES = () => {
  return {
    type: "LOAD_WEAHTER_API_CITES"
  };
};

export const FINISH_LOADING_WEAHTER_API_CITES = () => {
  return {
    type: "FINISH_LOADING_WEAHTER_API_CITES"
  };
};

export const loadCitesWeather = (lat, lon) => dispatch => {
  const cairo = require("../Apis/CitiesCoords").CitesCoords.cairo;
  const apikey = "ae9ccb1f3500a6435f46b51eb13ae8b0";
  dispatch(LOAD_WEAHTER_API_CITES());
  return Axios.get(
    `https://api.openweathermap.org/data/2.5/weather?lat=${cairo.lat}&lon=${
      cairo.lon
    }&appid=${apikey}`
  )
    .then(data => {
      dispatch({
        type: "GET_CAIRO_WEATHER",
        payload: data.data.main.temp
      });
      dispatch(FINISH_LOADING_WEAHTER_API_CITES());
    })
    .catch(err => {
      dispatch({
        type: "GET_CAIRO_WEATHER",
        payload: "fail to load data as " + err
      });
    });
};

