import React, { Component } from "react";
import { Col, Button, Row, MDBIcon } from "mdbreact";
import { Link } from "react-router-dom";
import SearchForm from "./Search.form";
import { Menubar, LiMembeer } from "./MenuToggler/Menu";
class Navbar extends Component {
  state = {
    toggler: false,
    togglerClass: ""
  };

  togglerState = () => {
    if (this.state.toggler) {
      return { maxWidth: "225px" };
    } else {
      return { maxWidth: 0 };
    }
  };

  onClickToggler = () => {
    this.setState({ toggler: !this.state.toggler });
  };

  componentDidMount() {}

  render() {
    return (
      <div>
        <nav
          className="p-3 z-depth-1 fixed-top"
          style={{ backgroundColor: "#fff" }}
        >
          <Row className="px-2">
            <div className="menu-div d-flex flex-row col-sm-2 col-4 p-0 position-relative justify-content-center">
              <button
                className="menu-Toggler mt-2"
                onClick={this.onClickToggler}
              >
                <i className="material-icons" style={{ fontSize: "2rem" }}>
                  menu
                </i>
              </button>
            </div>
            <Row className="col-sm-10 col-8 ml-sm-0 ml-auto row-no-padding">
              <Col size={12} lg={7} md={8} sm={8} className="mt-sm-0 my-3">
                <h2 className="menu-header cptl text-sm-center text-right pl-lg-5 ml-lg-5 mt-sm-2 m-0 p-0">
                  daily weather
                </h2>
              </Col>
              <Col sm={4} className="text-right d-none d-sm-block ml-auto">
                <SearchForm className="" />
              </Col>
            </Row>
          </Row>
        </nav>
        <Menubar style={this.togglerState()}>
          <LiMembeer
            conentlist="weeakly weather"
            offset={-300}
            iconName="timeline"
            scrollName="weather_daily"
          />
          <LiMembeer
            conentlist="Timeline"
            iconName="insert_chart"
            offset={-100}
            scrollName="timeline"
          />
          <LiMembeer
            conentlist="forecasts"
            iconName="history"
            offset={-100}
            scrollName="forecasts"
          />
          <LiMembeer
            conentlist="Subscribe"
            iconName="subscriptions"
            offset={-150}
            scrollName="subscribe"
          />
        </Menubar>
      </div>
    );
  }
}

export default Navbar;
