import React from "react";
class WeatherCards extends React.Component {
  state = {
    isload: false
  };

  render(props) {
    return (
      <div
        name="weather_daily"
        className={
          this.props.className +
          " d-flex flex-column justify-content-around text-center col-md-2 col-2 mt-5"
        }
      >
        <div>
          <h5>{this.props.day}</h5>
          <h5>{this.props.hour}</h5>
        </div>
        <div>
          <i className="material-icons">{this.props.status}</i>
        </div>
        <div>
          <h5>{this.props.temperature}</h5>
        </div>
      </div>
    );
  }
}

export default WeatherCards;
