import React from "react";
import { Container, Row, Col, Input } from "mdbreact";
import { Link } from "react-scroll";

const footer = () => {
  return (
    <Container fluid className="footer py-5">
      <Container>
        <Row>
          <Col className="col-md-4 col-12 text-md-left text-sm-center text-center my-md-0 my-3">
            <h2 className="my-4 upper">sitemap</h2>
            <ul className="list-group">
              <Link to={"weather_daily"} smooth={true} spy={true} offset={-300}>
                <li className="list-group-item pl-0">weakely weather</li>
              </Link>
              <Link to={"timeline"} smooth={true} spy={true} offset={-100}>
                <li className="list-group-item pl-0">Timeline</li>
              </Link>
              <Link to={"forecasts"} smooth={true} spy={true} offset={-100}>
                <li className="list-group-item pl-0">Forecasts</li>
              </Link>
              <Link to={"subscribe"} smooth={true} spy={true} offset={-150}>
                <li className="list-group-item pl-0">subscribe</li>
              </Link>
            </ul>
          </Col>
          <Col className="col-md-5  col-12 text-md-left text-sm-center text-center my-md-0 my-3">
            <h2 className="my-4 upper">Copy Rights</h2>
            <p className="lead cptl">
              all copy rights reserved to <br />
              {"\u00A9"} Daily Weather 2019
            </p>
            <p className="lead cptl">
              contact with author
              <span className="lower">
                <br />
                islam.s.mhmd1998@gmail.com
                <br />
                +201006885750
              </span>
            </p>

            <ul className="d-flex flex-row list-unstyled text-center justify-content-md-start justify-content-center">
              <li className="mr-4">
                <a
                  href="https://www.facebook.com/islamsaeed1998"
                  style={{ color: "unset" }}
                >
                  {" "}
                  <i className="fab fa-facebook fa-2x" />{" "}
                </a>
              </li>
              <li>
                <a
                  href="https://github.com/eslamSaeed12"
                  style={{ color: "unset" }}
                >
                  <i className="fab fa-github fa-2x" />{" "}
                </a>
              </li>
            </ul>
          </Col>
          <Col className="col-md-3  text-sm-center text-center my-md-0 my-3">
            <h2 className="my-4 upper">search</h2>
            <div>
              <form className="position-relative">
                <Input
                  className="form-control form-control-sm ml-3 w-100"
                  type="text"
                  placeholder="Search"
                  aria-label="Search"
                />
                <a
                  href="javascript:void(0)"
                  className="white-color link-yellow"
                >
                  <i className="fa fa-search position-absolute" />
                </a>
              </form>
            </div>
          </Col>
        </Row>
      </Container>
    </Container>
  );
};

export default footer;
