import React from "react";
import Tolltips from "react-tooltip";
import Coockies from "js-cookie";
import { connect } from "react-redux";
import * as action from "../../../../Actions/WeatherNow";
import { HashLoader } from "react-spinners";
class MainCard extends React.Component {
  state = {
    day: "",
    isload: false
  };

  UnixToTextDay(unixdate) {
    const dayDate = new Date(unixdate * 1000);
    return dayDate.toString().substr(0, 3);
  }
  getlocationFromCockies() {
    const location = Coockies.get("Location");
    return location;
  }

  hashUntilLoad() {
    if (!this.props.state.cityLoad) {
      return <HashLoader size={60} />;
    }
    if (this.props.state.cityLoad) {
      return (
        <div className={this.props.className}>
          <div className="top-header-day rounded py-2 text-center">
            <h5>{this.UnixToTextDay(this.props.state.cityData.dt)}</h5>
          </div>
          <div className="text-center">
            <h3>{this.props.state.cityData.name}</h3>
          </div>
          <div className="d-flex flex-row justify-content-around">
            <h2 className="display-4 big-icon pt-3">
              {Math.floor(this.props.state.cityData.main.temp - 273 )+ "\u00B0\u0043"}
            </h2>
            <div>
              <i className="display-4 material-icons main-icon-weather-status">
                {this.props.statusIcon}
              </i>
              <h6 className="mt-1 text-center font-weight-bold">{this.props.state.cityData.weather[0].description}</h6>
            </div>
          </div>
          <div className="mt-4">
            <ul className="nav">
              <li
                className="nav-item mr-3 font-size-as-icons-size mx-auto"
                data-tip="Wind Speed"
              >
                <Tolltips place="left" type="dark" />
                <i className="fas fa-wind i-small">{this.props.percent}</i>
                &nbsp;
                {this.props.state.cityData.wind.speed + "KM"}
              </li>

              <li
                className="nav-item font-size-as-icons-size  mx-auto"
                data-tip="Wind Direction"
                htmlFor="test"
              >
                <Tolltips place="right" type="dark" id="test" />
                <i className="fa fa-compass i-small" />
                &nbsp; {Math.floor(this.props.state.cityData.wind.deg) +"\u00B0" }
              </li>
            </ul>
          </div>
        </div>
      );
    }
  }

  render() {
    return this.hashUntilLoad();
  }
}

const mapPropsToState = state => {
  return {
    state: state
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetCityWeather: dispatch(action.nowCityTemp())
  };
};

export default connect(
  mapPropsToState,
  mapDispatchToProps
)(MainCard);
