import React from "react";
import ReactDOMServer from "react-dom/server";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import "./Components/main_styles/main.scss";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import { Provider } from "react-redux";
import { applyMiddleware, createStore } from "redux";
import mainreducer from "./Reducers/Main_Reducer";
import loger from "redux-logger";
import Thunk from "redux-thunk";

const store = createStore(mainreducer, applyMiddleware(loger, Thunk));

const AppBaseline = () => {
  return (
    <Provider store={store}>
      <App />
    </Provider>
  );
};

ReactDOMServer.renderToNodeStream(<AppBaseline />, document.getElementById("root"))

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
