import React from "react";
import { Container, Input, Button } from "mdbreact";

const subscribe = () => {
  return (
    <Container fluid className="subscribe py-5" name="subscribe">
      <Container>
        <h1 className="display-4 MerryFont text-center">
          Want To Know Weather News ?
        </h1>
        <form>
          <div>
            <div className="form-group col-md-8 col-sm-10 col-12 mx-auto">
              <Input className="my-5" type="text" label="Full name" icon="user" />
              <Input type="email" label="Email" icon="envelope" />
              <Button className="rounded-bttn">subscribe</Button>
            </div>
          </div>
        </form>
      </Container>
    </Container>
  );
};
export default subscribe;
