import React from "react";
import { Button } from "mdbreact";
import { Link } from "react-router-dom";
const BoxContent = () => {
  return (
    <div className="py-sm-5 py-3 px-5">
      <h1 className="display-3 font-weight-bold white-color">Daily Weather</h1>
      <h2 className="h3-responsive cptl white-color">
        Real Time weather 24h forecasts
      </h2>
      <Button color="warning" className="btn-warning-custom rounded">
        <Link to={"/home"} style={{ color: "unset" }}>
          try now
        </Link>
      </Button>
    </div>
  );
};
export default BoxContent;
