import React, { Component } from "react";
import { Container } from "mdbreact";
import Navigation from "./components/Navbar/Navigation.bar";
import Header from "./components/Header/Header";
import WeatherCards from "./components/weatherCards/Weather.cards";
import CityCard from "./components/WeatherByCity/CityWeather";
import SubscribeForm from "./components/subscribe/subscribe";
import WeatherCharts from "./components/StatsCharts/weatherTimeline";
import Footer from "./components/footer/footer";
import { HashLoader } from "react-spinners";

class StaticSite extends Component {
  render() {
    return (
      <Container fluid className="p-0">
        <Navigation />
        <Header />
        <WeatherCards />
        <CityCard />
        <WeatherCharts />
        <SubscribeForm />
        <Footer />
      </Container>
    );
  }
}

export default StaticSite;
