import React, { PureComponent } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from "recharts";
import { connect } from "react-redux";
import { chartsCall } from "../../../../Actions/WeatherNow";
import { BeatLoader } from "react-spinners";
const data = [
  {
    name: "Page A",
    uv: 4000,
    pv: 2400,
    amt: 2400
  },
  {
    name: "Page B",
    uv: 3000,
    pv: 1398,
    amt: 2210
  },
  {
    name: "Page C",
    uv: 2000,
    pv: 9800,
    amt: 2290
  },
  {
    name: "Page D",
    uv: 2780,
    pv: 3908,
    amt: 2000
  },
  {
    name: "Page E",
    uv: 1890,
    pv: 4800,
    amt: 2181
  },
  {
    name: "Page F",
    uv: 2390,
    pv: 3800,
    amt: 2500
  },
  {
    name: "Page G",
    uv: 3490,
    pv: 4300,
    amt: 2100
  }
];

class Example extends PureComponent {
  state = {
    temp_: true,
    humid: false,
    wind: false,
    all: false
  };
  getCity() {}
  data_2 = () => {
    if (this.props.state.charts_load) {
      const loc = this.props.state.wind.map((item, index) => {
        return {
          day: this.props.state.day[index],
          hour: this.props.state.hours[index].join(" "),
          temperature: this.props.state.temp[index],
          humidity: this.props.state.humidity[index],
          "wind speed": item
        };
      });
      return loc;
    }
  };
  render() {
    if (this.props.state.charts_load) {
      return (
        <div className="mt-5" style={{overflow:"hidden"}}>
          <ResponsiveContainer width="99%" height={350}>
            <LineChart
              className="mx-auto"
              width={1200}
              height={400}
              data={this.props.state.charts_load ? this.data_2() : data}
              margin={{
                top: 5,
                right: 30,
                left: 20,
                bottom: 5
              }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="day" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Line
                type="monotone"
                dataKey="humidity"
                stroke="#d8b504"
                activeDot={{ r: 8 }}
              />
              <Line
                type="monotone"
                dataKey="wind speed"
                stroke="#03bfbf"
                activeDot={{ r: 8 }}
              />
              <Line
                type="monotone"
                dataKey="temperature"
                stroke="#d8313f"
                activeDot={{ r: 8 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      );
    } else {
      return <BeatLoader size={20} margin={"2px"} />;
    }
  }
}

const mapStateToProps = state => {
  return {
    state: state
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onCallCharts: dispatch(
      chartsCall()
    )
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Example);
