import React from "react";
import { Col, Card, CardImage, Button } from "mdbreact";

class CardImg extends React.Component {
  state = {
    collapse: false
  };

  toggleCollapse = () => {
    this.setState({ collapse: !this.state.collapse });
  };
  render(props) {
    return (
      <Col className="col-lg-4 col-md-6 col-sm-12 my-md-4 my-sm-3 my-4">
        <Card>
          <div className="card-overlay">
            <CardImage
              zoom
              className={"rounded hoverThrough "}
              src={this.props.cityimage}
            />
            <div className="position-absolute title-card">
              <h2 className="upper white-color font-weight-bold ">
                {this.props.city}
              </h2>
              <h2 className="upper white-color font-weight-bold colorful-header mt-2">
                {this.props.cityTemp}
              </h2>
            </div>
          </div>
        </Card>
      </Col>
    );
  }
}

export default CardImg;
