import React from "react";
import { Alert, Button } from "mdbreact";

const AskForCookiesAlert = props => {
  return (
    <div className={props.className} style={props.style} id={props.id}>
      <div className="d-flex flex-row justify-content-center">
        <Alert dismiss className="alerto  cptl white-color">
          we using cookies to get better user experince
          {props.br}
          <Button
            className="mx-4 rounded btn-suc z-top"
            color="success"
            onClick={props.action}
            
          >
            Okay
          </Button>
        </Alert>
      </div>
    </div>
  );
};

export default AskForCookiesAlert;
